.confirm {
    color: white;
    @include flex(0, flex-start, center, column);
    min-height: calc(100vh - 315px);
    width: fit-content;
    margin: auto;
    padding-top: 50px;

    .boxes-wrapper {
        @include flex(20px, center, center)
    }

    .h3-wrapper {
        h3 {
            margin-bottom: 10px;
            color: $main;
            font-weight: 100;
        }

        .info-wrapper {
            width: 300px;
            border: 2px solid $bright-back;
            border-radius: 10px;
            @include flex(0, center, center);
            height: 60px;
            padding: 0 10px;

            p {

                flex: 1;
                color: white;
                text-align: center;
                font-size: 24px;
                margin: 0;
            }

            img {
                width: 40px;
            }
        }
    }

    .desc-wrapper {
        width: 100%;

        h3 {
            margin-bottom: 10px;
            color: $main;
        }

        div {
            width: 100%;
            border: 2px solid $bright-back;
            border-radius: 10px;
            padding: 10px;
            font-size: 24px;
            min-height: 60px;

        }
    }

    .checkbox-wrapper {
        @include flex(10px, flex-start, center);
        margin-top: 40px;
        align-self: flex-start;
        margin-bottom: 60px;
        position: relative;

        p {
            margin: 0;
            font-size: 24px
        }

        .checkbox {
            @include flex(0, center, center);
            width: 40px;
            height: 40px;
            border: 1px solid $bright-back;

            span {
                font-size: 40px;
            }
        }
    }

    .buttons {
        @include flex(0, center, center);
        margin-top: 30px;

        .accept,
        .reject {
            width: 210px;
            text-align: center;
            color: white;
            font-size: 24px;
            padding: 15px;
            cursor: pointer;
        }

        .accept {
            background-color: $main;
            border-radius: 10px;
        }
    }

    .same {
        width: min-content;
        margin-top: 30px;

        p {
            font-size: 24px;
            margin: 0;
            margin-bottom: 10px;
            text-align: center;
        }

        .copy {
            @include flex(10px, center, center);
            width: fit-content;
            margin: auto;
            border: 4px solid $bright-back;
            border-radius: 10px;
            padding: 5px 10px;

            span {
                text-align: center;
                font-size: 20px;
                color: $gray;
                text-decoration: underline;
            }

            img {
                width: 20px;
                aspect-ratio: 1/1;
                cursor: pointer;
            }
        }
    }
}

@media (max-width: $mobile) {
    .confirm {
        padding-top: 10px;
        width: 100%;

        .h3-wrapper {
            h3 {
                font-size: 16px;
                margin-bottom: 4px;
                margin-top: 0
            }

            .info-wrapper {
                @include flex(0, center, center);
                height: 40px;
                padding: 0 5px;

                p {
                    font-size: 16px;
                }

                img {
                    width: 25px;
                }
            }
        }

        .boxes-wrapper {
            flex-direction: column;
        }

        .desc-wrapper {
            width: 300px;
        }

        .checkbox-wrapper {
            align-self: center;
            margin: 20px 0;
            width: 300px;

            .checkbox {
                width: 30px;
                height: 30px;

                span {
                    font-size: 24px;
                }
            }

            p {
                font-size: 16px;
            }

        }

        .buttons {

            .accept,
            .reject {
                width: 120px;
                font-size: 16px;
            }
        }

        .same {
            padding: 10px;

            p {
                font-size: 16px;
            }

            .copy {
                width: 300px;

                span {
                    font-size: 16px;
                    overflow-x: hidden;
                }

                img {
                    width: 16px;
                }
            }
        }
    }
}