.event-card {
    background-color: $mid-back;
    border: 3px solid $bright-back;
    border-radius: 10px;
    margin-bottom: 10px;
    margin-top: 10px;

    .event-upper {
        padding: 14px;
        background-color: #090C1F;
        border-top-left-radius: 10px;
        border-top-right-radius: 10px;
        @include flex(0, space-between, center);

        h3 {
            margin: 0;
            font-size: 24px
        }

        div {
            @include flex(40px, center, center);

            p {
                margin: 0;
                font-size: 16px;
                cursor: pointer;
            }

            a {
                color: white;
                text-decoration: none;
            }
        }
    }

    .event-inner {
        @include flex(25px, flex-start, flex-start);
        font-size: 20px;
        padding: 20px;

        img {
            width: 150px;
            height: 250px;
            object-fit: cover;
            border-radius: 12px;
        }

        .headers {
            height: 250px;
            @include flex(10px, space-between, flex-start, column);

            p {
                margin: 0;
                font-weight: 300;
                color: white;
                width: max-content;
            }
        }

        .details {
            height: 250px;
            @include flex(10px, space-between, flex-start, column);
            opacity: 0.8;

            p {
                margin: 0;
                font-weight: 300;
            }

            img {
                width: 25px;
                height: 25px;
                cursor: pointer;
            }

        }
    }
}